import React, { Component } from 'react'
import {Link} from 'react-router-dom';
export default class Login extends Component {
    render() {
        return (
            <div basename="/build" style={{background:'white',height:'200px'}}>
                <p style={{width:'900px',height:'40px',background:'#f6f6f6',lineHeight:'3em',paddingLeft:'5px'}}>
                    <Link to='/home' style={{color:'#80bd01'}}>主页</Link>
                    <span>/</span>
                    <Link to='/login' style={{color:'#888'}}>登录</Link>
                </p>
                <div style={{marginLeft:'200px'}}>
                    用户名 <input type='text'/>
                    <p></p>
                    密&nbsp;&nbsp;&nbsp; 码 <input type='password'/>
                    <br/>
                    <div style={{marginTop:'40px',marginLeft:'50px'}}>
                        <Link to='/home/all/1' style={{background:'#08c',color:'white',borderRadius:'5px',padding:'5px 15px'}}>登录</Link>
                    </div>
                </div>
            </div>
        )
    }
}
