import React, { Component } from 'react'

export default class Topic extends Component {
    constructor(){
        super();
        this.state = {
            data: []
        }
    }
    componentDidMount(){        
        let url = 'https://cnodejs.org/api/v1/topics';
        fetch(url)
            .then((res)=>res.json())
            .then((res)=>{
                this.setState({
                    data: res.data
                });
                console.log(this.state.data);
            })  
    }
    render() {
        var url = this.props.location.pathname.slice(7);
        return (
            <div>
                {
                    this.state.data.map((item)=>{
                        if(url == item.id) {
                            console.log(item);
                            return <div key={item.id} dangerouslySetInnerHTML={{__html:item.content}}></div> || null
                        }
                    })         
                }
            </div>
        )
    }
}
